let locale = navigator.language.split('-')[0]

export const i18n = new VueI18n({
    lazy: true,
    locale : locale,
    messages : messages,
    defaultLocale: 'en',
    fallbackLocale: 'en'
  } 
)

import welcome from './components/Page_welcome.vue'
import ANarchi2D from './components/Page_anArchi2d.vue'
const routes = [
  { path: '/', redirect: '/welcome' },
  { path: '/welcome', component: welcome },
  { path: '/archi2D', component: ANarchi2D },//, beforeEnter: (to, from, next) => {} },
  { path: '*', redirect: '/welcome' },
]

const router = new VueRouter({
  mode: 'history',
  routes: routes
})








window.bus = new Vue({
  i18n,
  router,
  render: h => h(App),
}).$mount('#app')

