				//create FIXED ressources anim for the all app
				this.dragArrowfixedResources = this.anims.create(
					{
						key: 'dragArrow',
						frames: this.anims.generateFrameNumbers('dragArrow'),
						frameRate: 1,
						repeat: -1
					}
				)

				// declare selectBox
				this.selection = this.add.rectangle(0, 0, 0, 0, 0x1d7196, 0.5)
				
				// Create animation for all JSON_properties.items where type = 'spritesheet'
				for (let i = 0; i < JSON_properties.items.length; i++)
				{ 
					const item = JSON_properties.items[i]
					if (JSON_properties.resources.filter(s  => s.id === item.id)[0].type === 'spritesheet')
					{
						let anim = this.anims.create(
							{
								key: JSON_properties.items[i].GUID,
								frames: this.anims.generateFrameNumbers( JSON_properties.items[i].id),
								frameRate: Number(JSON_properties.items[i].frameRate),
								repeat: -1,
								//showOnStart: true
							}
						)
						anim.GUID = JSON_properties.items[i].GUID
						spriteAnims.push( anim )
					}
				}
				this.anims.pauseAll();

				//var shape = this.make.graphics().fillCircle(100, 100, 600);
				//var mask = shape.createGeometryMask();
				//mask.invertAlpha = JSON_properties.layers[i].invertAlpha

				//***  Create sprite ***//
				layerScene = this.add.layer();	

				//TODO : sort by zindex && VERIFY saved zIndex (from update too!)
				for (let i = 0; i <JSON_properties.items.length; i++)
				{
					const sprite = JSON_properties.items[i]
					let s = this.add.sprite( sprite.x, sprite.y )
					s
					.setDepth(sprite.zIndex)
					.setFlipX(Boolean(sprite.flipX))
					.setScale(sprite.scale)
					.setAlpha(sprite.alpha)
					.setTintFill(sprite.tint) 
					.setOrigin(0.5)
					.setInteractive( {draggable: true} )
					.on('pointerdown', function (){   
						if (!getVueInstance.designMode) return
						getVueInstance.isDragHandleArrow  = false
						getVueInstance.spriteDrag = false	
						getVueInstance.spriteMouseDown = true
						
					})
					.on('pointerup', function (){   
						if (!getVueInstance.designMode) return 
						getVueInstance.isDragHandleArrow  = false
						getVueInstance.spriteDrag = false	
						getVueInstance.spriteMouseDown = false
					})
					.on("dragstart", function(){
						if (!getVueInstance.designMode) return
						getVueInstance.isDragHandleArrow  = false
						getVueInstance.spriteDrag = true // false preserve spriteSelected
						getVueInstance.spriteMouseDown = true		
					})	
					.on("drag", function(pointer, dragX, dragY){
						if (!getVueInstance.designMode) return
						getVueInstance.isDragHandleArrow  = false
						getVueInstance.spriteDrag = true	
						getVueInstance.spriteMouseDown = true
						// multiple selection is dragging
						const distX = dragX - this.x  
						const distY = dragY - this.y  
						for (let f = 0; f < clone_spritesSelected.length; f++){
							sprites.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].x += distX
							sprites.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].y += distY
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].x += distX
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].y +=  distY
							if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween.isTweenY1Locked === true){ 
									JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween.y1 =  JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].y
								}
								if ((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].x) <= (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween.x1)){
									JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = false
									clone_spritesSelected.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = false
										
								} else {
									JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = true
									clone_spritesSelected.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = true
										
								}
							}
						}
						propertiesUpdate()
					})
					.on("dragend", function(){
						if (!getVueInstance.designMode) return
						getVueInstance.isDragHandleArrow  = false
						getVueInstance.spriteDrag = false
						getVueInstance.spriteMouseDown = false	
					})
					.play(JSON_properties.items[i].GUID)
					s.GUID = JSON_properties.items[i].GUID
					s.id = sprite.id
					sprites.push (s)
					layerScene.add(s)
				}	

				//console.table(this.sys.displayList.getChildren());

				//***  Create masks  ***/
				for (let i = 0; i <JSON_properties.layers.length; i++)
				{
					graphics.push(this.add.graphics())
					graphicsMask.push(this.make.graphics().fillStyle(0x000000))
					for (let j = 0; j <JSON_properties.layers[i].zones.length; j++)
					{   
						const polygon = new Phaser.Geom.Polygon(
							JSON_properties.layers[i].zones[j].coords
						)
						this.mask = new Phaser.Display.Masks.BitmapMask(this, graphicsMask[i].fillPoints(polygon.points, true))
						// invertAlpha=true -> HIDE inside masks = obstacles / invertAlpha=false -> SHOW ONLY inside masks = sky, etc.
						this.mask.invertAlpha = JSON_properties.layers[i].invertAlpha
						this.mask.GUID = JSON_properties.layers[i].GUID
						masks[i] = this.mask
						// Draw polygon as Mask filled with color (invisible by default) -> fillPoints + strokePoints
						graphics[i]
						.fillStyle(JSON_properties.layers[i].color, 0.4).lineStyle(0, JSON_properties.layers[i].color)
						.fillPoints(polygon.points, true).strokePoints(polygon.points, true)
						.setVisible(true) 
					}
				}
				
				this.input	
				.on('pointerdown', handlePointerDown, this)
				.on('pointermove', handlePointerMove, this)
				.on('pointerup', handlePointerUp, this)
				//.on('gamein', gamein, this)
				//.on('gameout', gameout, this)
				.on('pointerdownoutside', pointerdownoutside, this)
				.on('pointerupoutside', pointerupoutside, this)
				function pointerdownoutside (pointer)
				{
					getVueInstance.isPipette = false
					getVueInstance.isCopyScale = false
					
					document.body.style.cursor = 'default'

					if (!getVueInstance.designMode) return
					getVueInstance.sceneMouseDown = false
					getVueInstance.spriteMouseDown = false
					getVueInstance.spriteDrag  = false
					getVueInstance.isDragHandleArrow  = false
					this.selection.x = pointer.x
					this.selection.y = pointer.y
				}
				function pointerupoutside ()
				{
					if (!getVueInstance.designMode) return
					getVueInstance.sceneMouseDown = false
					getVueInstance.spriteMouseDown = false
					getVueInstance.spriteDrag  = false
					getVueInstance.isDragHandleArrow  = false
					this.selection.width = 0
					this.selection.height = 0
				}
				function handlePointerDown (pointer)
				{
					if (!getVueInstance.designMode) return
					if (getVueInstance.isDragHandleArrow) return
					//this.selection.x = pointer.x
					//this.selection.y = pointer.y
					//this.selection.width = 0
					//this.selection.height = 0
					//getVueInstance.spriteInSelection = false
					getVueInstance.sceneMouseDown = false
					
					/*const selectionRect = new Phaser.Geom.Rectangle(
						pointer.x,
						pointer.y,
						0,
						0
					)
					SelectedBox(selectionRect)*/


					//if (getVueInstance.spriteMouseDown) {
						this.selection.x = pointer.x
						this.selection.y  = pointer.y
						this.selection.width  = 0
						this.selection.height  = 0
						const selectionRect = new Phaser.Geom.Rectangle(
							this.selection.x,
							this.selection.y,
							this.selection.width,
							this.selection.height
						)
						SelectedBox(selectionRect, pointer.x, pointer.y)
					//}
				}
				function handlePointerMove(pointer)
				{
					if (!getVueInstance.designMode) return
					if (getVueInstance.isDragHandleArrow) return
					if (!pointer.isDown){
						return
					}

					
					if (getVueInstance.spriteDrag && getVueInstance.spriteMouseDown) {
						this.selection.x = pointer.x
						this.selection.y  = pointer.y
						this.selection.width  = 0
						this.selection.height  = 0
						const selectionRect = new Phaser.Geom.Rectangle(
							this.selection.x,
							this.selection.y,
							this.selection.width,
							this.selection.height
						)
						SelectedBox(selectionRect, pointer.x, pointer.y)		
					} else {
						//getVueInstance.sceneMouseDown = false
						const dx = pointer.x - pointer.prevPosition.x 
						const dy = pointer.y - pointer.prevPosition.y
						this.selection.width += dx
						this.selection.height += dy
					
						// create a new Rectangle
						const selectionRect = new Phaser.Geom.Rectangle(
							this.selection.x,
							this.selection.y,
							this.selection.width,
							this.selection.height
						)
						// check if width or height is negative
						// and then adjust
						if (selectionRect.width < 0)
						{
							selectionRect.x += selectionRect.width
							selectionRect.width *= -1
						}
						if (selectionRect.height < 0)
						{
							selectionRect.y += selectionRect.height
							selectionRect.height *= -1
						}
						//getVueInstance.clone_selectionRect = selectionRect
						SelectedBox(selectionRect, pointer.x, pointer.y)		
					}
								
					
				}
				function handlePointerUp()
				{
					if (!getVueInstance.designMode) return
					// selection is empty
					this.selection.x = 0
					this.selection.y = 0
					this.selection.width = 0
					this.selection.height = 0
					
					//getVueInstance.spriteInSelection = false
					getVueInstance.spriteMouseDown = false
					getVueInstance.spriteDrag = false
					getVueInstance.sceneMouseDown = false
					
					if (getVueInstance.isDragHandleArrow === true){
						return
					}
				}
				function SelectedBox(selectionRect, pointerX, pointerY) 
				{
					if (!getVueInstance.designMode) return

					/* isPipette */
					if (getVueInstance.isPipette === true){
						getVueInstance.isPipette = false
						document.body.style.cursor = 'default'; 
						let spritesSelected_IfNotPipette = sprites.filter(sprite => {
							return Phaser.Geom.Rectangle.Overlaps(selectionRect, sprite.getBounds())
						})
						if ((spritesSelected_IfNotPipette.length === 1) && (selectionRect.width===0) && (selectionRect.height===0)){
							tmpArray = []
							for (let f = 0; f < spritesSelected_IfNotPipette.length; f++){
								tmpArray.push(
								{ 
									GUID: spritesSelected_IfNotPipette[f].GUID,
									index: layerScene.getChildren().findIndex(item => item.GUID === spritesSelected_IfNotPipette[f].GUID)
								}
								)
							}
							tmpArray.sort(function (a, b) {
								return a.index - b.index
							})
							const tmp = spritesSelected_IfNotPipette.filter(item  => item.GUID === tmpArray[tmpArray.length-1].GUID)[0]
							spritesSelected_IfNotPipette = []
							spritesSelected_IfNotPipette.push(tmp)

							//change selected opacity & color
							getVueInstance.itemOpacity  = String(Math.floor(spritesSelected_IfNotPipette[0].alpha * 100))
							getVueInstance.itemColor = Math.abs(spritesSelected_IfNotPipette[0].tint).toString(16)

							for (let i = 0; i < spritesSelected.length; i++){
								JSON_properties.items[sprites.indexOf(spritesSelected[i])].alpha = Number(getVueInstance.itemOpacity ) / 100
								sprites[sprites.indexOf(spritesSelected[i])].setAlpha( JSON_properties.items[sprites.indexOf(spritesSelected[i])].alpha )

								JSON_properties.items[sprites.indexOf(spritesSelected[i])].tint = getVueInstance.itemColor 
								sprites[sprites.indexOf(spritesSelected[i])].setTintFill( '0x' + JSON_properties.items[sprites.indexOf(spritesSelected[i])].tint )
							}
						}
						return
					} 

					/* isCopyScale */
					if (getVueInstance.isCopyScale === true){
						getVueInstance.isCopyScale = false
						document.body.style.cursor = 'default'; 
						let spritesSelected_IfNotCopyScale = sprites.filter(sprite => {
							return Phaser.Geom.Rectangle.Overlaps(selectionRect, sprite.getBounds())
						})
						if ((spritesSelected_IfNotCopyScale.length === 1) && (selectionRect.width===0) && (selectionRect.height===0)){
							tmpArray = []
							for (let f = 0; f < spritesSelected_IfNotCopyScale.length; f++){
								tmpArray.push(
								{ 
									GUID: spritesSelected_IfNotCopyScale[f].GUID,
									index: layerScene.getChildren().findIndex(item => item.GUID === spritesSelected_IfNotCopyScale[f].GUID)
								}
								)
							}
							tmpArray.sort(function (a, b) {
								return a.index - b.index
							})
							const tmp = spritesSelected_IfNotCopyScale.filter(item  => item.GUID === tmpArray[tmpArray.length-1].GUID)[0]
							spritesSelected_IfNotCopyScale = []
							spritesSelected_IfNotCopyScale.push(tmp)
						
							//change selected scale
							getVueInstance.itemScale  = String(Math.floor(spritesSelected_IfNotCopyScale[0].scale * 100))

							for (let i = 0; i < spritesSelected.length; i++){
								JSON_properties.items[sprites.indexOf(spritesSelected[i])].scale = Number(getVueInstance.itemScale ) / 100
								sprites[sprites.indexOf(spritesSelected[i])].setScale( JSON_properties.items[sprites.indexOf(spritesSelected[i])].scale )
							}
						}
						return
					} 

					if (!getVueInstance.spriteDrag){
						// use the new Rectangle to check for overlap
						spritesSelected = sprites.filter(sprite => {
							return Phaser.Geom.Rectangle.Overlaps(selectionRect, sprite.getBounds())
						})
						// select on top depth sprite -> layerScene.getChildren().findIndex
						if ((spritesSelected.length>1) && (selectionRect.width===0) && (selectionRect.height===0)){
							tmpArray = []
							for (let f = 0; f < spritesSelected.length; f++){
								tmpArray.push(
								{ 
									GUID: spritesSelected[f].GUID,
									index: layerScene.getChildren().findIndex(item => item.GUID === spritesSelected[f].GUID)
								}
								)
							}
							tmpArray.sort(function (a, b) {
								return a.index - b.index
							})
							const tmp = spritesSelected.filter(item  => item.GUID === tmpArray[tmpArray.length-1].GUID)[0]
							spritesSelected = []
							spritesSelected.push(tmp)
						}
					}
					//sort spritesSelected by distance : from near to far
					if (spritesSelected.length){
						for (let f = 0; f < spritesSelected.length; f++){
							const distX = spritesSelected[f].x - pointerX  
							const distY = spritesSelected[f].y - pointerY
							spritesSelected[f].dist = Math.abs(distX) + Math.abs(distY)
						}
						spritesSelected.sort(function (a, b) {
							return a.dist - b.dist
						})
					}
					if (spritesSelected.length){
						if (spritesSelected[0].tween === undefined){
							for (let f = 0; f < spriteDragArrows.length; f++){
								spriteDragArrows[f].removeFromDisplayList()
								spriteDragArrows[f].removeInteractive()	
							}
							spriteDragArrows = []
						}
					}

					// set clone_spritesSelected 
					if (spritesSelected.length === 0) {
						clone_spritesSelected = []
					} else {
						let tmp = true
						for (let f = 0; f < spritesSelected.length; f++){
							if (clone_spritesSelected.includes(spritesSelected[f])===false){
								tmp = false
								continue
							}
						}
						if (tmp === false) {
							clone_spritesSelected = [...spritesSelected]
						}
					}
					propertiesUpdate()
				}
				function propertiesUpdate() {
					if (!getVueInstance.designMode) return
					// single or multiple selection => update GUI properties
					switch (clone_spritesSelected.length) {
					case 0: 
					{
						getVueInstance.tabProperties = getVueInstance.tabAnimation = false
						
						getVueInstance.itemX  = ""
						getVueInstance.itemY  = ""
						
						getVueInstance.itemColor  = 0x000000
						document.querySelector('.vc-compact-dot').style.visibility = 'hidden'
						
						getVueInstance.itemOpacity  = ""
						getVueInstance.itemScale  = ""
						getVueInstance.itemDuration  = ""

						getVueInstance.itemX1  = ""
						getVueInstance.itemY1  = ""

						getVueInstance.isTweenY1LockedDisable = true
						getVueInstance.isTweenY1Locked  = true
						getVueInstance.isTweenY1LockedIndeterminate  = false

						getVueInstance.itemFlipX  = false
						getVueInstance.isFlipXIndeterminate = true
						getVueInstance.isFlipXLocked = true

						getVueInstance.itemTweenRepeat  = false
						getVueInstance.isTweenRepeatIndeterminate = true
						getVueInstance.isTweenRepeatLocked = true
						
						getVueInstance.itemTweenYoyo  = false
						getVueInstance.isTweenYoyoIndeterminate = true
						getVueInstance.isTweenYoyoLocked = true
						
						getVueInstance.itemTweenFlipX  = false
						getVueInstance.isTweenFlipXIndeterminate = true
						getVueInstance.isTweenFlipXLocked = true
						
						getVueInstance.itemFPS  = ""	

						getVueInstance.itemDelay = ""
						getVueInstance.itemRepeatDelay = ""
						break
					}	
					case 1: 
					{
						getVueInstance.tabProperties = true
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.tabAnimation = true
						} else {
							getVueInstance.tabAnimation = false
						}

						getVueInstance.itemX  = String(Math.floor(clone_spritesSelected[0].x))
						getVueInstance.itemY  = String(Math.floor(clone_spritesSelected[0].y))

						document.querySelector('.vc-compact-dot').style.visibility = 'visible'
						getVueInstance.itemColor  = Math.abs(clone_spritesSelected[0].tint).toString(16)
						
						getVueInstance.itemOpacity  = String(Math.floor(clone_spritesSelected[0].alpha * 100))
						getVueInstance.itemScale  = String(Math.floor(clone_spritesSelected[0].scale * 100))
						
						getVueInstance.itemFlipX  = Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].flipX)
						getVueInstance.isFlipXIndeterminate = false
						getVueInstance.isFlipXLocked = false


						// itemDuration
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.itemDuration  = String(Math.floor(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.duration / 1000))
						} else {
							getVueInstance.itemDuration = "" 
						} 
						
						// itemX1
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.itemX1  = String(Math.floor(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.x1))
						} else {
							getVueInstance.itemX1 = "" 
						} 
						//itemY1
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.itemY1  = String(Math.floor(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.y1))
						} else {
							getVueInstance.itemY1 = ""
						} 
						
						//isTweenY1Locked
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.isTweenY1Locked  = Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.isTweenY1Locked)
							if (getVueInstance.isTweenY1Locked === true){
								getVueInstance.isTweenY1LockedDisable = true
							} else {
								getVueInstance.isTweenY1LockedDisable = false
							}
							getVueInstance.isTweenY1LockedIndeterminate  = false
						} else {
							getVueInstance.isTweenY1LockedDisable = true
							getVueInstance.isTweenY1Locked = false 
							getVueInstance.isTweenY1LockedIndeterminate  = true
						} 
						


						// itemTweenRepeat
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.isTweenRepeatIndeterminate = false
							getVueInstance.isTweenRepeatLocked = false
							getVueInstance.itemTweenRepeat  = Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.repeat)
						} else {
							getVueInstance.itemTweenRepeat = false 
							getVueInstance.isTweenRepeatIndeterminate = true
							getVueInstance.isTweenRepeatLocked = true
						} 

						// itemTweenYoyo
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.isTweenYoyoIndeterminate = false
							getVueInstance.isTweenYoyoLocked = false
							getVueInstance.itemTweenYoyo  = Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.yoyo)
						} else {
							getVueInstance.itemTweenYoyo = false
							getVueInstance.isTweenYoyoIndeterminate = true
							getVueInstance.isTweenYoyoLocked = true
						} 

						// itemTweenFlipX
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.isTweenFlipXIndeterminate = false
							getVueInstance.isTweenFlipXLocked = false
							getVueInstance.itemTweenFlipX  = Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.flipX)
						} else {
							getVueInstance.itemTweenFlipX = false
							getVueInstance.isTweenFlipXIndeterminate = true
							getVueInstance.isTweenFlipXLocked = true
						} 

						// itemFPS
						getVueInstance.itemFPS  = String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].frameRate)

						// itemDelay
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.itemDelay  = String(Math.floor(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.delay / 1000))
						} else {
							getVueInstance.itemDelay = "" 
						} 

						// itemRepeatDelay
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.itemRepeatDelay  = String(Math.floor(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
