						// itemX1
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined
							? tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.x1))
							: ""
						}
						([...new Set(tmpArray)].length === 1) 
						?  getVueInstance.itemX1 = [...new Set(tmpArray)][0] 
						: getVueInstance.itemX1 = ""
						
						// itemY1
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined
							? tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.y1))
							: ""
						}
						([...new Set(tmpArray)].length === 1) 
						?  getVueInstance.itemY1 = [...new Set(tmpArray)][0] 
