							} else {
								getVueInstance.isTweenY1LockedDisable = false
							}
							getVueInstance.isTweenY1LockedIndeterminate  = false
						} else {
							getVueInstance.isTweenY1LockedDisable = true
							getVueInstance.isTweenY1Locked = false
							getVueInstance.isTweenY1LockedIndeterminate  = true
						}

						// itemTweenRepeat
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.repeat))
