							getVueInstance.itemTweenRepeat = Boolean([...new Set(tmpArray)][0])
						} else {
							getVueInstance.itemTweenRepeat = false
							getVueInstance.isTweenRepeatIndeterminate = true
							getVueInstance.isTweeRepeatLocked = false
						}

						// itemTweenYoyo
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.yoyo))
							}
						}
						if ([...new Set(tmpArray)].length === 1){
							getVueInstance.isTweenYoyoIndeterminate = false;
							getVueInstance.isTweenYoyoLocked = false;
							getVueInstance.itemTweenYoyo = Boolean([...new Set(tmpArray)][0])
						} else {
							getVueInstance.itemTweenYoyo = false
							getVueInstance.isTweenYoyoIndeterminate = true
							getVueInstance.isTweenYoyoLocked = false
						}

						// itemTweenFlipX
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
