					}
					
				}

				// declare debug for the update event
				this.infos = this.add.text(10, 10, '', { fontFamily: 'Arial', fontSize: 14, color: '#00bb00' })  
				//declare canvas for the update() event
				this.canvas = this.add.graphics({ lineStyle: {  width: 1, color: 0x77B5FE, alpha: 1 } })	
			}, 






			update: function (time, delta)// (time, delta) //Pas nécessaires tant que caméra impossible
			{
				this.canvas.clear()

				//Pas nécessaires tant que caméra impossible
				this.controls.update(delta);

				//Get snapshot
				if (getVueInstance.isGetSnapshot === true) {
					getVueInstance.isGetSnapshot = false
					getVueInstance.designMode = true
					getVueInstance.changeDesignMode()
					if (document.getElementById('snapshotImg')) document.getElementById('snapshotImg').remove()
					this.renderer.snapshot(image => {
						image.style.width = '1024px'
						image.style.height = '764px'
						image.id="snapshotImg"
						document.getElementById('copyPhaserCanvasDiv').appendChild(image)
						document.getElementById('copyPhaserCanvasDiv').style = "width:1024px; height:764px;visibility:visible;z-index:2"
						document.getElementById('app_overlay').style.visibility = "visible"
						document.getElementById('dialogExport').showModal() 

						

						getVueInstance.initRecord()
					});
				}

				// Changing backgroundImage
				if (isChangingBackgroundImage && getVueInstance.designMode ){
					
					isChangingBackgroundImage = false				
					backgroundImage.destroy();
					if(this.textures.get('backgroundImage')){
						this.textures.remove('backgroundImage');
					}
					this.textures.once(Phaser.Textures.Events.LOAD, () => {
						backgroundImage = this.add.image((1024/2),(764/2), 'backgroundImage');
						layerBackground.add(backgroundImage)
						layerBackground.sendToBack(backgroundImage)
					})
					this.textures.addBase64('backgroundImage', imageData);
				}


				if (isDragFromToolBox.length && getVueInstance.designMode){
					pngLazyLoad.call(this, isDragFromToolBox[0])
					isDragFromToolBox=[]
				}	

				function pngLazyLoad(obj) {
					
					this.load.crossOrigin = 'anonymous'
					// png = 'assets/animated/man04_78x150'
					let asset =  obj.thumb.substring(0, obj.thumb.lastIndexOf("_thumb.png"))
					// id = 'man04'
					const id = asset.substring( asset.lastIndexOf("/") + 1, asset.lastIndexOf("_") )
					
					const tmp = asset.substring( asset.lastIndexOf("_") + 1, asset.length)
					// png = 'assets/animated/man04_78x150.png'
					asset += '.png'
					
					// w = 78
					const w = Number(tmp.substring( 0, tmp.indexOf("x")))
					// h = 150
					const h = Number(tmp.substring( tmp.indexOf("x") + 1 , tmp.length))

					//New element -> new ID !
					let GUID = Utils.CreateUUID()
					
					
					let tpl = { GUID:GUID, id:id, frameRate:obj.frameRate, x:0, y:0, tint:0x000000, alpha:1, flipX:false, scale:0.42, zIndex:9999 }
					if (obj.tween!== undefined){
						tpl.tween = { x1:0, y1:0, isTweenY1Locked:true, delay:0, duration: obj.tween.duration, repeatDelay:0, flipX:true, yoyo:true, repeat:true }
					}
					// Add the new item to original JSON
					JSON_properties.items.push (
						tpl
					) 
					
					// add to JSON a NEW "resource" if a NEW sprite is added on the scene
					if (JSON_properties.resources.filter(s  => s.src === asset)[0] === undefined){
						JSON_properties.resources.push (
							{ GUID:GUID, frameHeight:h, frameWidth:w, id:id, type:'spritesheet', src:asset }
						)
					}
					

					this.load.once('complete', ()=>{
						
						let anim = this.anims.create(
							{
								key: GUID,
								frames: this.anims.generateFrameNumbers(JSON_properties.items.filter(s  => s.GUID === GUID)[0].id),
								frameRate: tpl.frameRate,
								repeat: -1,
								showOnStart: true
							}
						)
						anim.GUID = GUID
						anim.pause()
						spriteAnims.push( anim )

						
						//const indexJSON_properties_items = JSON_properties.items.length-1
						//destroy this and REPLACE with an index property in sprite with value = i
						//const indexSprites = sprites.length-1
						
						let pointerX = 0
						let pointerY = 0
						const s = this.add.sprite( -200, -200 )
						.setDepth(9999)
						.setFlipX(false)
						.setScale(0.42)
						.setAlpha(1)
						.setTintFill(0x000000) 
						.setOrigin(0.5)
						.setInteractive({ draggable: true })
						.on('pointerdown', function (){    
							if (!getVueInstance.designMode) return
							getVueInstance.isDragHandleArrow  = false
							getVueInstance.spriteDrag = false	
							getVueInstance.spriteMouseDown = true
						})
						.on('pointerup', function (){   
							if (!getVueInstance.designMode) return 
							getVueInstance.isDragHandleArrow  = false
							getVueInstance.spriteDrag = false	
							getVueInstance.spriteMouseDown = false
							
						})
						.on("dragstart", function(){
							if (!getVueInstance.designMode) return
							getVueInstance.isDragHandleArrow  = false
							getVueInstance.spriteDrag = true // preserve spriteSelected
							getVueInstance.spriteMouseDown = true
							
						})	
						.on("drag", function(pointer, dragX, dragY){
							if (!getVueInstance.designMode) return
							pointerX = pointer.x
							pointerY = pointer.y

							getVueInstance.isDragHandleArrow  = false
							getVueInstance.spriteDrag = true	
							getVueInstance.spriteMouseDown = true
							// multiple selection is dragging
							const distX = dragX - this.x  
							const distY = dragY - this.y  
							for (let f = 0; f < clone_spritesSelected.length; f++){
								sprites.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].x += distX
								sprites.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].y += distY
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].x += distX
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].y +=  distY
								if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
									if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween.isTweenY1Locked === true){ 
										JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween.y1 =  JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].y
									}
									if ((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].x) <= (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween.x1)){
										JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = false
										clone_spritesSelected.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = false
											
									} else {
										JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = true
										clone_spritesSelected.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = true
											
									}
								}
							}
							propertiesUpdate()
						})
						.on("dragend", function(){
							if (!getVueInstance.designMode) return
							getVueInstance.isDragHandleArrow  = false
							getVueInstance.spriteDrag = false
							getVueInstance.spriteMouseDown = false
						})
						.play(GUID)
						s.GUID = GUID
						s.id = JSON_properties.items.filter(s  => s.GUID === GUID)[0].id
						sprites.push (s) 
						layerScene.add(s)
						
						spritesSelected.push(sprites[sprites.length - 1])
						
						//sort spritesSelected by distance : from near to far
						if (spritesSelected.length){
							for (let f = 0; f < spritesSelected.length; f++){
								const distX = spritesSelected[f].x - pointerX  
								const distY = spritesSelected[f].y - pointerY
								spritesSelected[f].dist = Math.abs(distX) + Math.abs(distY)
							}
							spritesSelected.sort(function (a, b) {
								return a.dist - b.dist
							})
						}
						if (spritesSelected.length){
							if (spritesSelected[0].tween === undefined){
								for (let f = 0; f < spriteDragArrows.length; f++){
									spriteDragArrows[f].removeFromDisplayList()
									spriteDragArrows[f].removeInteractive()	
								}
								spriteDragArrows = []
							}
						}
						clone_spritesSelected = [...spritesSelected]
					}, this);   
					
					this.load.spritesheet(id,  '' + asset, { frameWidth: w, frameHeight: h})
					// call complete() just ABOVE ! 
					this.load.start()

					// move the new created sprite when it is dragged from ToolBox into the Canvas
					this.input.on('pointermove', (pointer) => {
						if (( getVueInstance.isHoverCanvas ) && ( getVueInstance.isDnD_FromToolBox ))	{
							sprites[sprites.length - 1].setPosition(Math.floor(pointer.x), Math.floor(pointer.y))
							const j =  JSON_properties.items[sprites.length - 1]
							j.x = Math.floor(pointer.x)
							j.y = Math.floor(pointer.y)
							if (j.tween!== undefined){
								j.tween.x1 = Math.floor(pointer.x) + 200
								j.tween.y1 = Math.floor(pointer.y)
							}
							propertiesUpdate()
						}
					})
				}
				function propertiesUpdate() {
					
					if (!getVueInstance.designMode) return
					//if (spritesSelected.length === 0){
					//	clone_spritesSelected = []
					//}
					// single or multiple selection => update GUI properties
					switch (clone_spritesSelected.length) {
					case 0: 
					{
						getVueInstance.tabProperties = getVueInstance.tabAnimation = false
						
						getVueInstance.itemX  = ""
						getVueInstance.itemY  = ""
						
						getVueInstance.itemColor  = 0x000000
						document.querySelector('.vc-compact-dot').style.visibility = 'hidden'
						
						getVueInstance.itemOpacity  = ""
						getVueInstance.itemScale  = ""
						getVueInstance.itemDuration  = ""

						getVueInstance.itemX1  = ""
						getVueInstance.itemY1  = ""

						getVueInstance.isTweenY1LockedDisable = true
						getVueInstance.isTweenY1Locked  = true
						getVueInstance.isTweenY1LockedIndeterminate  = false

						getVueInstance.itemFlipX  = false
						getVueInstance.isFlipXIndeterminate = true
						getVueInstance.isFlipXLocked = true

						getVueInstance.itemTweenRepeat  = false
						getVueInstance.isTweenRepeatIndeterminate = true
						getVueInstance.isTweenRepeatLocked = true
						
						getVueInstance.itemTweenYoyo  = false
						getVueInstance.isTweenYoyoIndeterminate = true
						getVueInstance.isTweenYoyoLocked = true
						
						getVueInstance.itemTweenFlipX  = false
						getVueInstance.isTweenFlipXIndeterminate = true
						getVueInstance.isTweenFlipXLocked = true
						
						getVueInstance.itemFPS  = ""	

						getVueInstance.itemDelay = ""
						getVueInstance.itemRepeatDelay = ""
						break
					}	
					case 1: 
					{
						getVueInstance.tabProperties = true
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.tabAnimation = true
						} else {
							getVueInstance.tabAnimation = false
						}

						getVueInstance.itemX  = String(Math.floor(clone_spritesSelected[0].x))
						getVueInstance.itemY  = String(Math.floor(clone_spritesSelected[0].y))

						document.querySelector('.vc-compact-dot').style.visibility = 'visible'
						getVueInstance.itemColor  = Math.abs(clone_spritesSelected[0].tint).toString(16)
						
						getVueInstance.itemOpacity  = String(Math.floor(clone_spritesSelected[0].alpha * 100))
						getVueInstance.itemScale  = String(Math.floor(clone_spritesSelected[0].scale * 100))
						
						getVueInstance.itemFlipX  = Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].flipX)
						getVueInstance.isFlipXIndeterminate = false
						getVueInstance.isFlipXLocked = false


						// itemDuration
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.itemDuration  = String(Math.floor(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.duration / 1000))
						} else {
							getVueInstance.itemDuration = "" 
						} 
						
						// itemX1
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.itemX1  = String(Math.floor(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.x1))
						} else {
							getVueInstance.itemX1 = "" 
						} 
						//itemY1
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
							getVueInstance.itemY1  = String(Math.floor(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].
							tween.y1))
						} else {
							getVueInstance.itemY1 = ""
						} 
						
						//isTweenY1Locked
						if ((JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[0].GUID)[0].tween) !== undefined){
