							tmpArray.push(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween)
						}
						if ([...new Set(tmpArray)].length === 1){
							if (tmpArray[0] !== undefined){
								getVueInstance.tabAnimation = true
							} else {
								getVueInstance.tabAnimation = false
							}
						} else {
							getVueInstance.tabAnimation = true
						} 

						getVueInstance.itemX = (Utils.sameValueInArray(clone_spritesSelected,'x') === true) 
						? String(Math.floor(clone_spritesSelected[0].x)) : ""
					
						getVueInstance.itemY = (Utils.sameValueInArray(clone_spritesSelected,'y') === true) 
						? String(Math.floor(clone_spritesSelected[0].y)) : ""

						if (Utils.sameValueInArray(clone_spritesSelected,'tint') === true){
								document.querySelector('.vc-compact-dot').style.visibility = 'visible'
								getVueInstance.itemColor  = Math.abs(clone_spritesSelected[0].tint).toString(16)
						} else {
								getVueInstance.itemColor  = 0x000000
								document.querySelector('.vc-compact-dot').style.visibility = 'hidden'
						}
						
						getVueInstance.itemScale = (Utils.sameValueInArray(clone_spritesSelected,'scale') === true) 
						? String(clone_spritesSelected[0].scale*100) : ""
						
						getVueInstance.itemOpacity = (Utils.sameValueInArray(clone_spritesSelected,'alpha') === true) 
						? String(clone_spritesSelected[0].alpha*100) : ""

						getVueInstance.itemFlipX  = (Utils.sameValueInArray(clone_spritesSelected,'flipX') === true) 
						? Boolean(clone_spritesSelected[0].flipX) : false

						// itemFlipX
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].flipX))
						}
						if ([...new Set(tmpArray)].length === 1){
							getVueInstance.isFlipXIndeterminate = false;
							getVueInstance.isFlipXLocked = false;
							getVueInstance.itemFlipX = Boolean([...new Set(tmpArray)][0]);
						} else {
							getVueInstance.itemFlipX = false
							getVueInstance.isFlipXIndeterminate = true
							getVueInstance.isFlipXLocked = false
						} 

						// itemDuration
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.duration / 1000))
							}
						}
						([...new Set(tmpArray)].length === 1) 
						?  getVueInstance.itemDuration = [...new Set(tmpArray)][0] 
						: getVueInstance.itemDuration = ""
						
						// itemX1
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined
							? tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.x1))
							: ""
						}
						([...new Set(tmpArray)].length === 1) 
						?  getVueInstance.itemX1 = [...new Set(tmpArray)][0] 
						: getVueInstance.itemX1 = ""
						
						// itemY1
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined
							? tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.y1))
							: ""
						}
						([...new Set(tmpArray)].length === 1) 
						?  getVueInstance.itemY1 = [...new Set(tmpArray)][0] 
						: getVueInstance.itemY1 = ""

						// isTweenY1Locked
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.isTweenY1Locked))
							}
						}
						if ([...new Set(tmpArray)].length === 1){
							getVueInstance.isTweenY1Locked = Boolean([...new Set(tmpArray)][0])
							if (getVueInstance.isTweenY1Locked === true){
								getVueInstance.isTweenY1LockedDisable = true
							} else {
								getVueInstance.isTweenY1LockedDisable = false
							}
							getVueInstance.isTweenY1LockedIndeterminate  = false
						} else {
							getVueInstance.isTweenY1LockedDisable = true
							getVueInstance.isTweenY1Locked = false
							getVueInstance.isTweenY1LockedIndeterminate  = true
						}

						// itemTweenRepeat
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.repeat))
							}
						}
						if ([...new Set(tmpArray)].length === 1){
							getVueInstance.isTweenRepeatIndeterminate = false;
							getVueInstance.isTweenRepeatLocked = false;
							getVueInstance.itemTweenRepeat = Boolean([...new Set(tmpArray)][0])
						} else {
							getVueInstance.itemTweenRepeat = false
							getVueInstance.isTweenRepeatIndeterminate = true
							getVueInstance.isTweeRepeatLocked = false
						}

						// itemTweenYoyo
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.yoyo))
							}
						}
						if ([...new Set(tmpArray)].length === 1){
							getVueInstance.isTweenYoyoIndeterminate = false;
							getVueInstance.isTweenYoyoLocked = false;
							getVueInstance.itemTweenYoyo = Boolean([...new Set(tmpArray)][0])
						} else {
							getVueInstance.itemTweenYoyo = false
							getVueInstance.isTweenYoyoIndeterminate = true
							getVueInstance.isTweenYoyoLocked = false
						}

						// itemTweenFlipX
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.flipX))
							}
						}
						if ([...new Set(tmpArray)].length === 1){
							getVueInstance.isTweenFlipXIndeterminate = false;
							getVueInstance.isTweenFlipXLocked = false;
							getVueInstance.itemTweenFlipX = Boolean([...new Set(tmpArray)][0]);
						} else {
							getVueInstance.itemTweenFlipX = false
							getVueInstance.isTweenFlipXIndeterminate = true
							getVueInstance.isTweenFlipXLocked = false
						} 

						// itemFPS
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].frameRate))
						}
						([...new Set(tmpArray)].length === 1) 
						?  getVueInstance.itemFPS = [...new Set(tmpArray)][0] 
						: getVueInstance.itemFPS = ""

						// itemDelay
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.delay / 1000))
							}
						}
						([...new Set(tmpArray)].length === 1) 
						?  getVueInstance.itemDelay = [...new Set(tmpArray)][0] 
						: getVueInstance.itemDelay = ""
						
						// itemRepeatDelay
						tmpArray = []
						for (let f = 0; f < clone_spritesSelected.length; f++){
							if (JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
								tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.repeatDelay / 1000))
							}
						}
						([...new Set(tmpArray)].length === 1) 
						?  getVueInstance.itemRepeatDelay = [...new Set(tmpArray)][0] 
						: getVueInstance.itemRepeatDelay = ""
						break
					}

					}
					
				}

				if (getVueInstance.debug && getVueInstance.designMode){
					let debug = ''

					let minutes = Math.floor(elapsedTime / 60);
					let seconds = Math.floor(elapsedTime - (minutes * 60));
					debug += "elapsedTime: " +  minutes + ':' +  seconds + "\n";

					debug+= 'MouseX : ' + MouseX + ' MouseY:' + MouseY + "\n"
					debug+= 'sprites[3] pointermove x: ' + sprites[3].x + ' y:' + sprites[3].y + "\n"
					
					debug+= 'isDnD_FromToolBox: ' + getVueInstance.isDnD_FromToolBox + "\n"
					debug+= 'isHoverCanvas: ' + getVueInstance.isHoverCanvas + "\n"

					debug+= 'isTweening[3]: ' + this.tweens.isTweening(sprites[3]) + "\n"
					if (this.tweens.getTweensOf(sprites[3],true)[0]){
						debug+= 	'tween[3] : Start=' + this.tweens.getTweensOf(sprites[3],true)[0].data[0].start + 
										' End=' + this.tweens.getTweensOf(sprites[3],true)[0].data[0].end
						+ "\n" + "\n"
					}

					//debug+= 'Is tweening[3]: ' + getVueInstance.mouse.x + "\n"

					//let allSprites = this.children.list.filter(x => x instanceof Phaser.GameObjects.Sprite)
					debug+= (this.game.loop.actualFps | 0) + "fps \n" 
					debug+= 'Nb Sprites: ' + sprites.length + "\n" 
					debug+= 'designMode: ' + getVueInstance.designMode + "\n" 
					debug+= 'displayLayers: ' + getVueInstance.displayLayers + "\n" 
					
					debug+= 'sceneMouseDown: ' + getVueInstance.sceneMouseDown +  "\n" 
					
					debug+= 'spriteMouseDown: ' + getVueInstance.spriteMouseDown +  "\n" 
					//debug+= 'spriteInSelection: ' + getVueInstance.spriteInSelection +  "\n" 
					debug+= 'spriteDrag: ' + getVueInstance.spriteDrag +  "\n" 
					debug+= 'isDragHandleArrow: ' + getVueInstance.isDragHandleArrow +  "\n\n" 
							
					debug+= '\nspritesSelected: ' + "\n" 
					for (let i = 0; i < spritesSelected.length; i++)
					{
						debug+= spritesSelected[i].GUID + " || " + spritesSelected[i].GUID + "\n" 
					}

					debug+= '\nclone_spritesSelected: ' + "\n" 
					for (let i = 0; i < clone_spritesSelected.length; i++)
					{
						debug+= clone_spritesSelected[i].GUID + " || " + clone_spritesSelected[i].GUID  + "\n" 
					}
					
					
					debug+= "\n" + 'spriteDragArrows: ' + "\n" 
					for (let i = 0; i < spriteDragArrows.length; i++)
					{
						debug+= JSON.stringify(spriteDragArrows[i].GUID) + "\n" 
					}
					


					this.infos.setText(debug)
				} else {
					this.infos.setText('')
				}
				
				if (fullscreen){

					
						getVueInstance.designMode = false
					
						// masks or not ?
						for (let i = 0; i < JSON_properties.layers.length; i++)
						{
							getVueInstance.designMode ? layers[i].clearMask() : layers[i].setMask(masks[i])
						}

						// sprites play or pause => stop ?
						for (let i = 0; i < JSON_properties.items.length; i++)
						{
							const sprite = JSON_properties.items[i]
							getVueInstance.designMode ? sprites[i].setFrame(0).pause() : sprites[i].play(sprite.GUID)
						}
						
						
						// animations or not ?
						for (let i = 0; i < animations.length; i++)
						{
							getVueInstance.designMode ? animations[i].pause() : animations[i].restart()
						}
						// this.scale.setGameSize(sceneWidth*2, sceneHeight*2)
						//this.scale.setGameSize(window.innerWidth, window.innerHeight)
						
						fullscreen = false
						this.scale.startFullscreen()	
					
				}


				/*** SELECTED OBJECTS ***/
				const g = this.canvas
				if ( getVueInstance.designMode) {
					const  timer = (Date.now() % 600+1) / 600

					// Hack blendmodes : why not ?
					//g.setBlendMode(Phaser.BlendModes.MULTIPLY);

					/* draw sprites SELECTION ANIMATED and other sprites (OUT OF SELECTION) handles  ***/					
					for (let i = 0; i < sprites.length; i++){
						if ((i == sprites.length - 1) && ( !getVueInstance.isHoverCanvas ) && ( getVueInstance.isDnD_FromToolBox )){
							// isDnD_FromToolBox && !isHoverCanvas (= drag NEW sprite)
							// don't show any type of handles
							continue
						} 
						const bounds = sprites[i].getBounds()
						const rect = new Phaser.Geom.Rectangle(bounds.x, bounds.y, bounds.width, bounds.height)
						const points = []
						Phaser.Geom.Rectangle.Decompose(rect, points)	
						
						// draw NORMAL handles for OUT OF SELECTION sprites
						if ((spritesSelected.includes(sprites[i])===false) && (clone_spritesSelected.includes(sprites[i])===false)){
							// draw sprite selection rect
							g.lineStyle(2, 0x77B5FE, 1)
							g.fillStyle( 0x77B5FE, 1)
							g.strokeRectShape(bounds)
							// draw handles	
							for(let j = 0; j < points.length; j++){
								g.lineStyle(2, 0x77B5FE, 1)
								g.fillRect((points[j].x)-4,(points[j].y)-4, 8, 8)

								g.lineStyle(2, 0xFFFFFF, 1)
								g.strokeRect((points[j].x)-4,(points[j].y)-4, 8, 8)	
							}
						// draw NOT NORMAL handles for spritesSelected BUT timer drawDashedPolygon									
						} else {
							if (!getVueInstance.spriteDrag  ){
								const poly=[]
								for(let j = 0; j < points.length; j++){
									poly.push({x:Number(points[j].x), y:Number(points[j].y)})
								}
								g.lineStyle(2, 0xFFFFFF, 1)
								g.strokeRect(bounds.x, bounds.y, bounds.width, bounds.height)
								// Draw ANIMATED dashed selection
								g.lineStyle(2, 0x000000, 1)
								g.fillStyle( 0x000000, 1)
								Utils.drawDashedPolygon(g, poly, 0, 0, 0, 6, 6,timer)
								
								//draw handles
								for(let j = 0; j < points.length; j++)
								{
									g.lineStyle(2, 0x000000, 1)
									g.fillStyle( 0x000000, 1)
									g.fillRect(points[j].x-4,points[j].y-4, 8, 8)	

									g.lineStyle(2, 0xFFFFFF, 1)
									g.strokeRect((points[j].x)-4,(points[j].y)-4, 8, 8)	
								}
							}
						}
					}
					
					// Draw arrows from tweens animations	
					for (let i = 0; i < JSON_properties.items.length; i++){
						// isDnD_FromToolBox && !isHoverCanvas
						// don't show ARROWS of the future (?) animation sprite outside canvas zone
						if ((i == JSON_properties.items.length - 1) && ( !getVueInstance.isHoverCanvas ) && ( getVueInstance.isDnD_FromToolBox ))
							continue
						// arrows properties
						let arrowColor = 0x77B5FE
						if (JSON_properties.items[i].tween!== undefined){
							g.lineStyle(3, arrowColor)
							Utils.Arrow(
								g, 
								JSON_properties.items[i].x,
								JSON_properties.items[i].y,
								JSON_properties.items[i].tween.x1,
								JSON_properties.items[i].tween.y1,
							)
							g.lineStyle(2, arrowColor)
							Utils.dashedLine(
								g,
								JSON_properties.items[i].tween.x1,
								JSON_properties.items[i].tween.y1,
								JSON_properties.items[i].x,
								JSON_properties.items[i].y,
								[5,7,5,7]
							);	
						}
					}
							
					//draw selected arrow handle for Drag & Drop if ONLY 1 media is selected
					if (spritesSelected.length){
						//reinitialize spriteDragArrows[]
						if  ( (getVueInstance.spriteDrag)  || (spriteDragArrows.length === 0) && 
							((dragArrows.indexOf( spritesSelected[0].GUID) !== -1) || (spriteDragArrows.length === 0))
							|| ((JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].tween !== undefined) && (dragArrows[0].x !== JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].tween.x1) && (getVueInstance.isDragHandleArrow == false) )
							|| ((JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].tween !== undefined) && (dragArrows[0].y !== JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].tween.y1) && (getVueInstance.isDragHandleArrow == false) )
							){
								
							
							for (let i = 0; i < spriteDragArrows.length; i++){
								spriteDragArrows[i].removeFromDisplayList()
								spriteDragArrows[i].removeInteractive()	
							}

							spriteDragArrows = []

							// when dragging new item
							if  ( (!getVueInstance.isHoverCanvas ) && ( getVueInstance.isDnD_FromToolBox ))	return

							if (JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].tween === undefined) return
							
							dragArrows = []
							
							dragArrows.push({ GUID: spritesSelected[0].GUID, x: Number(JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].tween.x1), y: Number(JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].tween.y1)})
							
							for (let i = 0; i < dragArrows.length; i++){
								let s = this.add.sprite( dragArrows[i].x, dragArrows[i].y )
								.setOrigin(0.5)
								.setInteractive({ draggable: true })
								.on('pointerdown', function (){    
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false	
									getVueInstance.spriteMouseDown = true
									getVueInstance.isDragHandleArrow = true
								})
								.on('pointerup', function (){    
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false	
									getVueInstance.spriteMouseDown = false
									getVueInstance.isDragHandleArrow = false
									
									
								})
								.on("dragstart", function(){
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false
									getVueInstance.spriteMouseDown = true
									getVueInstance.isDragHandleArrow = true
								})	
								.on("drag", function(pointer){
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false
									getVueInstance.spriteMouseDown = true
									getVueInstance.isDragHandleArrow = true
									
									const distX = pointer.x - this.x  
									const distY = pointer.y - this.y  

									this.x = Math.floor(pointer.x)
									if (JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].tween.isTweenY1Locked === true){ 
										this.y = Math.floor(JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0].y)
									} else {
										this.y = Math.floor(pointer.y)
										JSON_properties.items.filter(item  => item.GUID === spritesSelected[0].GUID)[0].tween.y1 += distY
									}
									for (let f = 0; f < clone_spritesSelected.length; f++){			
										if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined){
											JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween.x1 += distX
											if ((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].x) <= (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].tween.x1)){
												JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = false
												clone_spritesSelected.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = false	
											} else {
												JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = true
												clone_spritesSelected.filter(item  => item.GUID === clone_spritesSelected[f].GUID)[0].flipX = true	
											}
										}
									}
									propertiesUpdate()
								})
								.on("dragend", function(){
									getVueInstance.sceneMouseDown = false
									getVueInstance.spriteDrag = false
									getVueInstance.spriteMouseDown = false
									getVueInstance.isDragHandleArrow = false
									
								})
								.play(this.dragArrowfixedResources)
								s.GUID = spritesSelected[0].GUID
								s.id = 'dragArrowfixedResources'
								spriteDragArrows.push (s) 
							}
						}
						// draw green arrow when spritesSelected.length and item has a tween
						let arrowColor = 0x00CC00
						const item = JSON_properties.items.filter(i  => i.GUID === spritesSelected[0].GUID)[0]
						if (item.tween !== undefined){
							g.lineStyle(3, arrowColor)
							Utils.Arrow(
								g, 
								item.x,
								item.y,
								item.tween.x1,
								item.tween.y1
							)
							g.lineStyle(3, arrowColor)
							Utils.dashedLine(
								g,
								item.tween.x1,
								item.tween.y1,
								item.x,
								item.y,
								[10,0.1]
							)
						}
					} else {
						for (let i = 0; i < spriteDragArrows.length; i++){
							spriteDragArrows[i].removeFromDisplayList()
							spriteDragArrows[i].removeInteractive()
						}
						spriteDragArrows = []
					}
				} else {
					// not in designMode
					for (let i = 0; i < spriteDragArrows.length; i++){
						spriteDragArrows[i].removeFromDisplayList()
						spriteDragArrows[i].removeInteractive()
					}
					spriteDragArrows = []
				}		
			},
			getVueInstance: function() {
				getVueInstance = this
			},
			test: function () {
				
			}
		},
		

		},
	}
  },
  created: function() {
	this.game.scene.getVueInstance.call(this)
  },
  mounted:function(){
	
	
	

	// when start then no itemColor !
	document.querySelector('.vc-compact-dot').style.visibility = 'hidden'

	sprites = []
	spritesSelected = []
	clone_spritesSelected = []

	spriteAnims = []
	animations = []
	graphics = []
	graphicsMask = []
	masks = []
	layers = []

	dragArrows = []
	spriteDragArrows = []

	//backgroundImage = null
	//layerBackground = null

	//imageData = Utils.defaultBackground()
	

	//console.log(Boolean(navigator.userAgent.indexOf("Firefox")))
	
	//TODO : remove comment if is onboading
	//this.$tours['myTour'].start()

  },
 
  methods: {
	setBackground(output){
		if (!getVueInstance.designMode) return
		this.hasImage = true
		imageData = output
		isChangingBackgroundImage = true
	},
	bringToTop(){
		if (!getVueInstance.designMode) return
		for (let i = 0; i < clone_spritesSelected.length; i++){
			layerScene.bringToTop(sprites.filter(s  => s.GUID === clone_spritesSelected[i].GUID)[0])	
		}
		let i=0
		layerScene.getChildren().forEach(function(sprite) {
			JSON_properties.items.filter(item  => item.GUID === sprite.GUID)[0].zIndex = i
			i++
		}, this);
	},
	sendToBack(){
		if (!getVueInstance.designMode) return
		for (let i = 0; i < clone_spritesSelected.length; i++){
			layerScene.sendToBack(sprites.filter(s  => s.GUID === clone_spritesSelected[i].GUID)[0])
		}
		let i=0
		layerScene.getChildren().forEach(function(sprite) {
			JSON_properties.items.filter(item  => item.GUID === sprite.GUID)[0].zIndex = i
			i++
		}, this);
		
	},
	showToolTipPhaser(index){
		if (!getVueInstance.designMode) return
		if (getVueInstance.isDnD_FromToolBox)  return

		const x = this.$refs.pngFile[index].getBoundingClientRect().left - (this.$refs.pngFile[index].getBoundingClientRect().width / 2) -10
		const y = (this.$refs.pngFile[index].getBoundingClientRect().top - this.$refs.pngFile[index].getBoundingClientRect().height) - 110 
					
		if ((isPreviewPhaserMedia.length===0) || (isPreviewPhaserMedia[0]!==this.$refs.pngFile[index].id)){
			isPreviewPhaserMedia.push('' + this.$refs.pngFile[index].id)
		}
		let elem = document.getElementById('TooltipPhaser') 
		elem.style.visibility = 'visible'
		elem.style.position = 'absolute'
		elem.style.left = x + "px"
		elem.style.top = y + "px"
	},     
	hideToolTipPhaser(){
		if (!getVueInstance.designMode) return
		let elem = document.getElementById('TooltipPhaser') 
		elem.style.visibility = 'hidden'
	},

	getPngFile (f) {
		return ""+ f
	},
		
	
	toolbox_dragStart(item){
		
		if (!getVueInstance.designMode) return
		
		getVueInstance.tabProperties = getVueInstance.tabAnimation = false

		//default color is black
		getVueInstance.itemColor  = 0x000000
		document.querySelector('.vc-compact-dot').style.visibility = 'visible'

		getVueInstance.isDnD_FromToolBox=true
		getVueInstance.onDropZonePhaser = false
		spritesSelected = []
		clone_spritesSelected = []
		getVueInstance.spriteDrag = true
		getVueInstance.spriteMouseDown = true
		isDragFromToolBox.push(item) 	
		
		//TODO : dry with leaveCanvas()
		getVueInstance.itemX  = ""
		getVueInstance.itemY  = ""
		
		getVueInstance.itemOpacity  = ""
		getVueInstance.itemScale  = ""
		getVueInstance.itemDuration  = ""

		getVueInstance.itemX1  = ""
		getVueInstance.itemY1  = ""
		getVueInstance.isTweenY1Locked = false

		getVueInstance.itemFlipX  = false
		getVueInstance.isFlipXIndeterminate = true
		getVueInstance.isFlipXLocked = true

		getVueInstance.itemTweenRepeat  = false
		getVueInstance.isTweenRepeatIndeterminate = true
		getVueInstance.isTweenRepeatLocked = true
		
		getVueInstance.itemTweenYoyo  = false
		getVueInstance.isTweenYoyoIndeterminate = true
		getVueInstance.isTweenYoyoLocked = true
		
		getVueInstance.itemTweenFlipX  = false
		getVueInstance.isTweenFlipXIndeterminate = true
		getVueInstance.isTweenFlipXLocked = true
		
		getVueInstance.itemFPS  = ""
		getVueInstance.itemDelay  = ""
		getVueInstance.itemRepeatDelay  = ""

		
		this.$tours['myTour'].nextStep()		
	},
	moveOnCanvas(){
		getVueInstance.isHoverCanvas = true
		if (!getVueInstance.designMode) return
		if (( getVueInstance.isHoverCanvas ) && ( getVueInstance.isDnD_FromToolBox ))	{
			sprites[sprites.length-1].setVisible(true)
		}

		if (spritesSelected.length === 0){
			getVueInstance.tabProperties = getVueInstance.tabAnimation = false
		} else {
			getVueInstance.tabProperties =  true

			tmpArray = []
			for (let f = 0; f < spritesSelected.length; f++){
				tmpArray.push(JSON_properties.items.filter(i  => i.GUID === spritesSelected[f].GUID)[0].tween)
			}
			if ([...new Set(tmpArray)].length === 1){
				if (tmpArray[0] !== undefined){
					getVueInstance.tabAnimation = true
				} else {
					getVueInstance.tabAnimation = false
				}
			} else {
				getVueInstance.tabAnimation = true
			} 
		}

		
	},
	leaveCanvas(){
		getVueInstance.isHoverCanvas = false
		if (!getVueInstance.designMode) return
		if ( getVueInstance.isDnD_FromToolBox )	{
			sprites[sprites.length-1].setVisible(false)
			sprites[sprites.length - 1].setPosition(-9999, -9999)
			
			getVueInstance.tabProperties = getVueInstance.tabAnimation = false

			getVueInstance.itemX  = ""
			getVueInstance.itemY  = ""
			
			getVueInstance.itemOpacity  = ""
			getVueInstance.itemScale  = ""
			getVueInstance.itemDuration  = ""
			
			getVueInstance.itemX1  = ""
			getVueInstance.itemY1  = ""
			getVueInstance.isTweenY1Locked = false
			getVueInstance.isTweenY1LockedIndeterminate = true


			getVueInstance.itemFlipX  = false
			getVueInstance.isFlipXIndeterminate = true
			getVueInstance.isFlipXLocked = true

			getVueInstance.itemTweenRepeat  = false
			getVueInstance.isTweenRepeatIndeterminate = true
			getVueInstance.isTweenRepeatLocked = true
			
			getVueInstance.itemTweenYoyo  = false
			getVueInstance.isTweenYoyoIndeterminate = true
			getVueInstance.isTweenYoyoLocked = true
			
			getVueInstance.itemTweenFlipX  = false
			getVueInstance.isTweenFlipXIndeterminate = true
			getVueInstance.isTweenFlipXLocked = true
			
			getVueInstance.itemFPS  = ""

			getVueInstance.itemDelay  = ""
			getVueInstance.itemRepeatDelay  = ""
		}	
	},
	toolbox_dragEnd() {

		

		if (!getVueInstance.designMode) return
		if (!getVueInstance.onDropZonePhaser){
			
			// if drop OUTSIDE phaser : destroy sprite and animation
			getVueInstance.itemColor  = 0x000000
			document.querySelector('.vc-compact-dot').style.visibility = 'hidden'

			JSON_properties.items.pop()
			//animations[animations.length-1].stop()
			//TODO : i must kill the animation !
			sprites[sprites.length-1].removeFromDisplayList()
			animations.pop()
			sprites.pop()
			spritesSelected = []
			clone_spritesSelected = []

			getVueInstance.tabProperties = getVueInstance.tabAnimation = false
			this.$tours['myTour'].previousStep()
		} else {
			getVueInstance.onDropZonePhaser = false
			this.$tours['myTour'].nextStep()
		}
		
		getVueInstance.isDnD_FromToolBox = false
		getVueInstance.spriteDrag = false
		getVueInstance.spriteMouseDown = false
		

	},
	DropZonePhaser() {
		if (!getVueInstance.designMode) {
			getVueInstance.onDropZonePhaser = false
		} else {
			getVueInstance.onDropZonePhaser = true
		}
		
	},
	changeDesignMode() {	

		this.$tours['myTour'].nextStep()

		this.displayLayers = getVueInstance.designMode 

		if (getVueInstance.designMode === true){
			if (clone_spritesSelected.length !== 0){
				getVueInstance.tabProperties  = true
			} else {
				getVueInstance.tabProperties  = false
			}
			
			if (clone_spritesSelected.length !== 0){
				let show_tabAnimation = false
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
						show_tabAnimation = true
						getVueInstance.tabAnimation = true
					}
				}
				if (show_tabAnimation === false) {
					getVueInstance.tabAnimation = false
				}
			} else {
				getVueInstance.tabAnimation = false
			}
		} else {
			getVueInstance.tabProperties  = false
			getVueInstance.tabAnimation = false
		}
		
		// displayLayers => masks, or not ?
		// clearmask en design mode, parce ce que sans, on ne pourrait pas voir certains sprites sous "obstacle"'
		for (let i = 0; i <JSON_properties.layers.length; i++){
			graphics[i].setVisible( this.displayLayers )
		}
		for (let i = 0; i < JSON_properties.items.length; i++){
			if (getVueInstance.designMode ){
				sprites[i].clearMask()
			} else {
				sprites[i].setMask( masks.filter(s  =>  s.GUID === JSON_properties.items[i].mask_GUID)[0] ) 
			}   
		}


		for (let i = 0; i < timers.length; i++){
			getVueInstance.game.instance.scene.scenes[0].time.removeEvent(timers[i]);
		}
		timers = []

		if (getVueInstance.designMode) {
			for (let i = 0; i < animations.length; i++){
				// seek(0) is stoppping animation : seek(0.01) is ok !
				animations[i].restart().seek( 0.01 ).pause() 
			}
		} else {	
			for (let i = 0; i < animations.length; i++){
				animations[i].remove()
			}
			animations = []
			for (let i = 0; i <JSON_properties.items.length; i++){
				if (JSON_properties.items[i].tween !== undefined ){
					const animation = getVueInstance.game.instance.scene.scenes[0].tweens.add(
						{ 
							targets: sprites[i],
							yoyo:  Boolean(JSON_properties.items[i].tween.yoyo),
							delay: Number(JSON_properties.items[i].tween.delay),
							x: {
								value: {
									getStart: function () { return Number(JSON_properties.items[i].x) },
									getEnd:   function () { return Number(JSON_properties.items[i].tween.x1) },
								},
								flipX: Boolean(JSON_properties.items[i].tween.flipX)
							},
							y: {
								value: {
									getStart: function () { return Number(JSON_properties.items[i].y) },
									getEnd:   function () { return Number(JSON_properties.items[i].tween.y1) },	
								}
							},	
							duration: Number(JSON_properties.items[i].tween.duration),
							persist: true,
							onDelay: () => {
								if 	(getVueInstance.designMode === false){
									if (Boolean(JSON_properties.items[i].tween.flipX) === true){
										sprites[i].setFlipX( Boolean(JSON_properties.items[i].flipX) )
									}
									sprites[i].setVisible(true)
								}
							},
							onComplete: () => {
								if 	(getVueInstance.designMode === false){ 
									if (Boolean(JSON_properties.items[i].tween.repeat) === true){ 
										// important : for sprites not flicker when delay and repeatDelay = 0 ELSE visible = false
										if ((Number(JSON_properties.items[i].tween.delay) !== 0) || (Number(JSON_properties.items[i].tween.repeatDelay) !== 0)){
											sprites[i].setVisible(false) 
										}
										timers.push( 
											getVueInstance.game.instance.scene.scenes[0].time.delayedCall (
												Number(JSON_properties.items[i].tween.repeatDelay), 
												() => {
													if (getVueInstance.designMode === false){
														animations[animations.findIndex(item  => item.GUID === JSON_properties.items[i].GUID)].restart() 
													}
												}, 
												[], this
											) 
										)
									}  else { 
										sprites[i].setVisible(false)
									} 
								}
							},
							onYoyo: () => {
								
									if (Boolean(JSON_properties.items[i].tween.yoyo) === true){
											sprites[i].setVisible(true)
									} else {
										if (Boolean(JSON_properties.items[i].tween.repeat) === false){
											sprites[i].setVisible(false)
										} else {
											if (getVueInstance.designMode === false){
												animations[animations.findIndex(item  => item.GUID === JSON_properties.items[i].GUID)].restart() 
												sprites[i].setVisible(true)
											}
										}
									}
								
							}
						}
					)
					animation.GUID = JSON_properties.items[i].GUID 
					animations.push( animation )
				}
			}
		} 

		

		//en design mode, remet le sprite en position de départ et visible
		for (let i = 0; i < JSON_properties.items.length; i++){
			if (getVueInstance.designMode) {
				sprites[i].setFlipX( JSON_properties.items[i].flipX ).setFrame(0).setX(JSON_properties.items[i].x).setY(JSON_properties.items[i].y).setVisible(true)
			} else {
				if (JSON_properties.items[i].tween !== undefined){
					sprites[i].setFrame(0).setX(-1000).setY(-1000).setVisible(false) 
				}
			}
		}

		// spriteAnims pause or resume
		for (let i = 0; i < spriteAnims.length; i++){
			getVueInstance.designMode ? spriteAnims[i].pause() : spriteAnims[i].resume()
		}

	},	
	DebugMode() {
		if (!getVueInstance.designMode) return
		this.debug = !this.debug
	},
	showLayers() {
		this.displayLayers = !this.displayLayers
		for (let i = 0; i <JSON_properties.layers.length; i++){
			graphics[i].setVisible( this.displayLayers )
		}
	},
	changeFullscreen() {
		fullscreen = true
	},
	setItemColor(itemColor) {
		if (!getVueInstance.designMode) return 
		//this.game.scene.setItemColor.call( this.game.scene,itemColor )
		for (let i = 0; i < clone_spritesSelected.length; i++){
			JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].color = '0x' + itemColor.hex.substring(1)
			sprites[sprites.indexOf(clone_spritesSelected[i])].setTintFill( '0x' + itemColor.hex.substring(1) )
		}
		document.querySelector('.vc-compact-dot').style.visibility = 'visible'
		getVueInstance.itemColor  = Math.abs(clone_spritesSelected[0].tint).toString(16)
	},
	setItemFlipX(){
		this.$tours['myTour'].nextStep()
		if (!getVueInstance.designMode) return 
		for (let i = 0; i < clone_spritesSelected.length; i++){			
			if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].flipX !== getVueInstance.itemFlipX){
				JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].flipX = getVueInstance.itemFlipX
				sprites[sprites.indexOf(clone_spritesSelected[i])].setFlipX( getVueInstance.itemFlipX )

				if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
					if ((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].x) >= (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1)){
						JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1 = Number(JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].x) - (Number(JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1) - Number(JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].x))
					} else {
						JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1 = Number(JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].x) + (Number(JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].x) - Number(JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1))
					}
					getVueInstance.itemX1 = String(JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1)
				}	
			}
		}
	},
	setItemTweenRepeat(){
		if (!getVueInstance.designMode) return 
		for (let i = 0; i < clone_spritesSelected.length; i++){
			if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
				JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.repeat = Boolean(getVueInstance.itemTweenRepeat)
			}
		}
	},
	setItemTweenYoyo(){
		if (!getVueInstance.designMode) return 
		for (let i = 0; i < clone_spritesSelected.length; i++){
			if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
				JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.yoyo = Boolean(getVueInstance.itemTweenYoyo)
				JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.flipX = Boolean(getVueInstance.itemTweenYoyo)
				getVueInstance.itemTweenFlipX = Boolean(getVueInstance.itemTweenYoyo)
			}
		}
	},
	setItemTweenFlipX(){
		if (!getVueInstance.designMode) return 
		for (let i = 0; i < clone_spritesSelected.length; i++){
			if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
				JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.flipX = Boolean(getVueInstance.itemTweenFlipX)
				JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.yoyo = Boolean(getVueInstance.itemTweenFlipX)
				getVueInstance.itemTweenYoyo = Boolean(getVueInstance.itemTweenFlipX)
			}
		}
	},
	setItemTweenY1Locked(){
		if (!getVueInstance.designMode) return 
		for (let i = 0; i < clone_spritesSelected.length; i++){
			if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
				if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].isTweenY1Locked !== getVueInstance.isTweenY1Locked){
					JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.isTweenY1Locked = Boolean(getVueInstance.isTweenY1Locked)
					if (getVueInstance.isTweenY1Locked === true){
						getVueInstance.isTweenY1LockedDisable = true
					} else {
						getVueInstance.isTweenY1LockedDisable = false
					}
					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.isTweenY1Locked === true){
						getVueInstance.itemY1 = String(JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].y)
						JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.y1 = Number(getVueInstance.itemY1)
					}
				}
			}
			
		}
	},

	test(){
		//isDragFromToolBox.push('tree') 
		//this.game.scene.test.call(this.game.scene)	
	},
	snapshot(){
		getVueInstance.isGetSnapshot = true
		this.$tours['myTour'].stop()
	},
	Recorder()  {
		
		// mode Play for captureStream
		getVueInstance.designMode = false
		getVueInstance.changeDesignMode()

		let fps = 60
		let videoStream = document.getElementById('phaserCanvas').captureStream(fps)
		mediaRecorder = new MediaRecorder(videoStream)
		let videoURL=""
		let chunks = []
		mediaRecorder.onstop = function() {
			if (isCancelExport === false){		
				let blob = new Blob(chunks, {
				'type': 'video/mp4'
				})
				chunks = []
				videoURL = URL.createObjectURL(blob)
				// eslint-disable-next-line no-console
				//console.log("mp4 video url:", videoURL);
				let myReader = new FileReader()
				myReader.readAsDataURL(blob)
				myReader.addEventListener("loadend", function(e) {
					document.getElementById('video').src = e.result
					document.getElementById('video').hidden = true
					const link = document.createElement("a")
					link.href = videoURL
					link.download = JSON_properties.project
					link.click();

					document.getElementById('dialogExport').close() 
					if (document.getElementById('snapshotImg')) document.getElementById('snapshotImg').remove()
					//copyPhaserCanvasDiv -> back to z-index:1
					document.getElementById('copyPhaserCanvasDiv').style.visibility = "hidden"
					document.getElementById('app_overlay').style.visibility = "hidden"

					getVueInstance.designMode = true
					getVueInstance.changeDesignMode()

					makeDialogDraggable()
					document.getElementById('dialogExportOK').showModal()
					
					
					document.getElementById('video').src = null
				});	
			}
		}
		mediaRecorder.ondataavailable = function(e) {
				chunks.push(e.data)
		}
		this.start = () => { mediaRecorder.start();  }
		this.stop = () => 	mediaRecorder.stop()
		this.pause = () => mediaRecorder.pause()
		this.resume = () => mediaRecorder.resume()
		this.getUrl = () => videoURL
	},
	dialogExportCancel(){
		isCancelExport = true
		mediaRecorder.stop()
		if (document.getElementById('snapshotImg')) document.getElementById('snapshotImg').remove()
		document.getElementById('copyPhaserCanvasDiv').style.visibility = "hidden"
		document.getElementById('app_overlay').style.visibility = "hidden"

		getVueInstance.designMode = true
		getVueInstance.changeDesignMode()
	},
	initRecord() {
		isCancelExport = false
		let rec = new getVueInstance.Recorder()
		setTimeout(rec.start, 60)
		setTimeout(rec.stop, Number(JSON_properties.mp4_duration));
	},
	setItem( property, minus_plus) {
		
		if (!getVueInstance.designMode) return

		let multiselection = false
		if ((minus_plus === "+") || (minus_plus === "-")){
			multiselection = true
		}

		switch (property) {
			case 'itemX':
			{
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (multiselection === true) {						
						if (minus_plus === "+"){
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].x = Number(Utils.convertToDec2(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].x)) + Number('1')
						} else {
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].x = Number(Utils.convertToDec2(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].x)) - Number('1')
						}
					} else {
						JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].x = Number(this.itemX)
					}
					sprites[sprites.indexOf(clone_spritesSelected[i])].setX(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].x)

					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
						if ((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].x) >= (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1)){
								JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].flipX = true
								sprites[sprites.indexOf(clone_spritesSelected[i])].setFlipX( true )
						} else {
								JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].flipX = false
								sprites[sprites.indexOf(clone_spritesSelected[i])].setFlipX( false )	
						}
					}		
				}

				tmpArray = []
				for (let f = 0; f < clone_spritesSelected.length; f++){
					tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].flipX))
				}
				if ([...new Set(tmpArray)].length === 1){
					getVueInstance.isFlipXIndeterminate = false;
					getVueInstance.isFlipXLocked = false;
					getVueInstance.itemFlipX = Boolean([...new Set(tmpArray)][0]);
				} else {
					getVueInstance.itemFlipX = false
					getVueInstance.isFlipXIndeterminate = true
					getVueInstance.isFlipXLocked = false
				} 
				break
			}
			case 'itemY':
			{
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (multiselection === true){		
						if (minus_plus === "+"){
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].y = Number(Utils.convertToDec2(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].y)) + Number('1')
						} else {
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].y = Number(Utils.convertToDec2(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].y)) - Number('1')
						}
					} else {
						JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].y = Number(this.itemY)	
					}
					
					sprites[sprites.indexOf(clone_spritesSelected[i])].setY( JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].y )

					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
						if ((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.isTweenY1Locked === true)) { 
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.y1 =JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].y
						}		
					}

					

				}

				// update GUI itemY1
				tmpArray = []
				for (let f = 0; f < clone_spritesSelected.length; f++){
					JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween !== undefined
					? tmpArray.push(String(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].tween.y1))
					: ""
				}
				([...new Set(tmpArray)].length === 1) 
				?  getVueInstance.itemY1 = [...new Set(tmpArray)][0] 
				:  ""

				break
			}
			case 'itemScale':
			{
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (multiselection === true){				
						if (minus_plus === "+"){
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale = Number(Utils.convertToDec2(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale)) + Number('0.01');
						} else {
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale = Number(Utils.convertToDec2(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale)) - Number('0.01');
						}
					} else {
						JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale = Number(this.itemScale ) / 100
					}
					// enable only > 0 -> Scale between 0% and N%
					if (JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale <= 0) {
						JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale = 0	
					}
					sprites[sprites.indexOf(clone_spritesSelected[i])].setScale( JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale )
				}
				// Show 0 when all selected sprites REACH this scale limit (negative values are forbidden)
				let duplicate = []
				for (let i = 0; i < clone_spritesSelected.length; i++){
					duplicate.push(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].scale)
				}
				let withoutDuplicate = [...new Set(duplicate)]
				if (withoutDuplicate.length === 1){
					if (withoutDuplicate[0] === 0){
						this.itemScale = String(withoutDuplicate[0] * 100)
					}
				}
				break
			} 
			case 'itemOpacity':
			{
				
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (multiselection === true){				
						if (minus_plus === "+"){
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha = Number(Utils.convertToDec2(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha)) + Number('0.01');
						} else {
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha = Number(Utils.convertToDec2(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha)) - Number('0.01');
						}
					} else {
						JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha = Number(this.itemOpacity ) / 100
					}
					// enable only between 0 and 1 -> Opacity between 0% and 100%
					if (JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha >= 1){
						JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha = 1
						
					} else if (JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha <= 0) {
						JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha = 0	
					}
					
					sprites[sprites.indexOf(clone_spritesSelected[i])].setAlpha( JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha )
				}
				// Show 0 or 100 when all selected sprites REACH these opacity limits	
				let duplicate = []
				for (let i = 0; i < clone_spritesSelected.length; i++){
					duplicate.push(JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].alpha)
				}
				let withoutDuplicate = [...new Set(duplicate)]
				if (withoutDuplicate.length === 1){
					if ((withoutDuplicate[0] === 0) || (withoutDuplicate[0] === 1)){
						this.itemOpacity = String(withoutDuplicate[0] * 100)
					}
				}
				break
			}
			case 'itemX1':
			{	
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
						if (multiselection === true){	
							if (minus_plus === "+"){
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1 = Number((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1)) + Number('1');
								
							} else {
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1 = Number((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1)) - Number('1');
							}
							
						} else {
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1 = Number (this.itemX1 ) 
							
						}
												
						if ((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].x) >= (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1)){
								JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].flipX = true
								sprites[sprites.indexOf(clone_spritesSelected[i])].setFlipX( true )	
						} else {
								JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].flipX = false
								sprites[sprites.indexOf(clone_spritesSelected[i])].setFlipX( false )	
						}			
					}
				}

				tmpArray = []
				for (let f = 0; f < clone_spritesSelected.length; f++){
					tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].flipX))
				}
				if ([...new Set(tmpArray)].length === 1){
					getVueInstance.isFlipXIndeterminate = false;
					getVueInstance.isFlipXLocked = false;
					getVueInstance.itemFlipX = Boolean([...new Set(tmpArray)][0]);
				} else {
					getVueInstance.itemFlipX = false
					getVueInstance.isFlipXIndeterminate = true
					getVueInstance.isFlipXLocked = false
				} 

				break
			} 
			case 'itemY1':
			{
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
						if (multiselection === true){	
							if (minus_plus === "+"){
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.y1 = Number((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.y1)) + Number('1');
								
							} else {
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.y1 = Number((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.y1)) - Number('1');
							}
							
						} else {
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.y1 = Number (this.itemY1 ) 
							
						}						
						if ((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].x) >= (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.x1)){
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].flipX = true
							sprites[sprites.indexOf(clone_spritesSelected[i])].setFlipX( true )	
						} else {
							JSON_properties.items[sprites.indexOf(clone_spritesSelected[i])].flipX = false
							sprites[sprites.indexOf(clone_spritesSelected[i])].setFlipX( false )	
						}			
					}
				}

				tmpArray = []
				for (let f = 0; f < clone_spritesSelected.length; f++){
					tmpArray.push(Boolean(JSON_properties.items.filter(i  => i.GUID === clone_spritesSelected[f].GUID)[0].flipX))
				}
				if ([...new Set(tmpArray)].length === 1){
					getVueInstance.isFlipXIndeterminate = false;
					getVueInstance.isFlipXLocked = false;
					getVueInstance.itemFlipX = Boolean([...new Set(tmpArray)][0]);
				} else {
					getVueInstance.itemFlipX = false
					getVueInstance.isFlipXIndeterminate = true
					getVueInstance.isFlipXLocked = false
				} 

				break
			} 
			case 'itemDuration':
			{
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
						if (multiselection === true){	
							if (minus_plus === "+"){
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.duration = Number((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.duration)) + Number('1000');
								
							} else {
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.duration = Number((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.duration)) - Number('1000');
							}
							
						} else {
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.duration = Number (this.itemDuration ) * 1000
							
						}
						// enable only > 0 -> Duration between 1 sec and Nsec
						if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.duration <= 0) {
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.duration = 1000	
						}
					}
				}
				break
			} 
			case 'itemFPS':
			{
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (multiselection === true){	
						if (minus_plus === "+"){
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate = Number(( JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate) ) + Number('1');
							
						} else {
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate = Number( (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate) ) - Number('1');
						}	
					} else {
						JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate = Number( this.itemFPS )
					}
					// enable only > 0 -> Duration between 1 sec and Nsec
					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate <= 0) {
						JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate = 1	
					}
					spriteAnims.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate = Number( JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate )
					// dynamicaly change sprites[x].anims.msPerFrame ->  msPerFrame = 1000/FPS
					sprites.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].anims.msPerFrame =  1000 / Number( JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].frameRate ) 
				}
				break
			} 
			case 'itemDelay':
			{
				for (let i = 0; i < clone_spritesSelected.length; i++){
					if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween !== undefined){
						if (multiselection === true){	
							if (minus_plus === "+"){
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.delay = Number((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.delay)) + Number('1000');
								
							} else {
								JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.delay = Number((JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.delay)) - Number('1000');
							}
						} else {
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.delay = Number (this.itemDelay ) * 1000
						}
						// enable only > 0 -> Delay between 1 sec and Nsec
						if (JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.delay <= 0) {
							JSON_properties.items.filter(item  => item.GUID === clone_spritesSelected[i].GUID)[0].tween.delay = 1000	
						}
					}
				}
				break
			} 

		}
	},
	pipette(){
		if (clone_spritesSelected.length !== 0){
			getVueInstance.isPipette = true
			document.body.style.cursor = 'crosshair'; 
		}
	},
	copyScale(){
		if (clone_spritesSelected.length !== 0){
			getVueInstance.isCopyScale = true
			document.body.style.cursor = 'crosshair'; 
		}
	},
	Menu( item ){
		switch (item) {
			case 'Contactez-nous':
			{
				alert('dominiquehamet703d@gmail.com')
				break
			}
			default : {
				alert(item)
				break
			}
		}
			
	},
	selectCountry(label){
		this.$i18n.locale = String(label.prefixe)
	}

	
	
  },
}


</script>

