<template>
<div
    
    
    style="border:transparent;margin-top:5px;"


    class="number-input"
    :class="{
      'number-input--inline': inline,
      'number-input--center': center,
      'number-input--controls': controls,
      [`number-input--${size}`]: size,
    }"
>
    <button
        v-if="controls"
        class="number-input__button number-input__button--minus"
        type="button"
        tabindex="-1"
        @mousedown='whileMouseDown(decreaseNumber)'
        @mouseup='clearTimer'
        @mouseleave="clearTimer"
        :disabled="disabled || readonly || !decreasable"
    />
    

    <!-- @keypress='validateInput'
    v-on:change="$emit('input', $event.target.value)"
    -->
    
     <!-- type="text" -->
    <vue-number
        ref="input"
        class="number-input__input"
        
        contenteditable="true"

        :name="name"
        
        v-bind="attrs"
        v-model="numericValue"
        v-on:change="change"
        @focus="focus"
        onclick="this.select()"
        
        :style="disabled ? 'background:#ddd' : 'background:#fff'"

        :decimal="decimal"
        :nullValue="nullValue"
        :separator="separator"
        :suffix="suffix"
        :precision=Number(precision)
        :masked=Boolean(masked)
        :prefill=Boolean(prefill)
        :min='min'
        :max='max'
        :readonly="readonly || !inputtable"
        :disabled="disabled || (!decreasable && !increasable)"
        :placeholder="placeholder"
        autocomplete="off"
        @paste="paste"    
    >
    </vue-number>

    <button
        v-if="controls"
        class="number-input__button number-input__button--plus"
        type="button"
        tabindex="-1"
        @mousedown='whileMouseDown(increaseNumber)'
        @mouseup='clearTimer'
        @mouseleave="clearTimer"
        :disabled="disabled || readonly || !increasable"  
    />
</div>
</template>

<script>
import { component as VueNumber } from '@coders-tm/vue-number-format'



const REGEXP_NUMBER = /^-?(?:\d+|\d+\.\d+|\.\d+)(?:[eE][-+]?\d+)?$/;
//const REGEXP_DECIMALS = /\.\d*(?:0|9){10}\d*$/;
//const normalizeDecimalNumber = (value, times = 100000000000) =>
  //REGEXP_DECIMALS.test(value) ? Math.round(value * times) / times : value;
  
var mouseDownSpeed = 50 

export default {
  name: 'mySpin',
  components: {
      VueNumber,
  },
  data: function() {
    return {
      numericValue: this.value,
      timer: null
    };
  },

  props: {
    attrs: {
      type: Object,
      default: undefined,
    },
    name: {
      type: String,
      default: undefined,
    },

    placeholder: {
      type: String,
      default: undefined,
    },

    center: Boolean,
    controls: Boolean,
    disabled: Boolean,
    inline: Boolean,
    readonly: Boolean,

    size: {
      type: String,
      default: undefined,
    },

    inputtable: {
      type: Boolean,
      default: true,
    },

    value: {
      type: String,
      default: ""
    },
    min: {
      default: -Infinity,
      type: Number
    },
    max: {
      default: Infinity,
      type: Number
    },
    step: {
      default: 1,
      type: Number
    },
    
    decimal: {
      default: undefined,
      type: String
    },
    nullValue: {
      default: undefined,
      type: String
    },
    separator: {
      default: undefined,
      type: String
    },
    suffix: {
      default: undefined,
      type: String
    },
    precision: {
      default: undefined,
      type: String
    },
    prefill: {
      type: Boolean,
      default: false,
    },
    masked: {
      default: false,
      type: Boolean
    }, 
  },
  computed: {
    increasable() {
       if (this.fromFormatToValue(this.numericValue) !== "") { 
        return Number(this.fromFormatToValue(this.numericValue)) < this.max;
       }
       return true
    },

    /**
     * Indicate if the value is decreasable.
     * @returns {boolean} Return `true` if it is decreasable, else `false`.
     */
    decreasable() {
       if (this.fromFormatToValue(this.numericValue) !== "") {  
        return Number(this.fromFormatToValue(this.numericValue)) > this.min;
       }
       return true
    }
  }, 
  watch: {
    value: {
      immediate: true,
      handler(newValue) {
        if (newValue !== this.currentValue) {
           this.numericValue = String(newValue)
        }
      }
    }
  },
  methods: {
     fromFormatToValue(s){
      return s.replaceAll(this.suffix, '')
    },
    change(){
        if (Number(this.fromFormatToValue(this.numericValue))  > this.max){
            this.numericValue = String(this.max)
        }
        if (Number(this.fromFormatToValue(this.numericValue))  < this.min){
            this.numericValue = String(this.min)
        }
        this.$emit('input',this.fromFormatToValue(this.numericValue))
        this.$emit('change')
    },
    focus(){
        /*if (isFocused == true) { 
          document.querySelectorAll('.number-input').forEach((result) => {
            
            result.classList.add('wrapper-focus')
            
          })
        }*/
    },
    clearTimer() {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
    },
    whileMouseDown(callback) {
      if (this.timer === null) {
        this.timer = setInterval(() => {
          callback();
        }, mouseDownSpeed);
      }
    },
    increaseNumber() {
        if (this.fromFormatToValue(this.numericValue) === "") {
             this.$emit('plus')
        } else {
            if ((Number(this.fromFormatToValue(this.numericValue)) + this.step) <= this.max){
                this.numericValue = String(Number(this.fromFormatToValue(this.numericValue)) + this.step)
            } else {
                this.numericValue = String(this.max)
            }
            this.$emit('plus')
        }
    },
    decreaseNumber() {
        if (this.fromFormatToValue(this.numericValue) === "") {
             this.$emit('minus')
        } else {
            if ((Number(this.fromFormatToValue(this.numericValue)) - this.step) >= this.min){
                this.numericValue = String(Number(this.fromFormatToValue(this.numericValue)) - this.step)
            } else {
                this.numericValue = String(this.min)
            }
            this.$emit('minus')
        }
    },
    paste(event) {
      const clipboardData = event.clipboardData || window.clipboardData;
      if (clipboardData && !REGEXP_NUMBER.test(clipboardData.getData("text"))) {
        event.preventDefault();
      }
    },
  }
}
</script>

<style scoped>


.number-input {
  display: block;
  font-size: 0;
  max-width: 100%;
  overflow: hidden;
  position: relative;
  
}
.number-input__button {
  background-color: #fff;
  border: 0;
  
  bottom: 2px;
  position: absolute;
  top: 2px;
  width: 2.5rem;
  z-index: 1;
  cursor: pointer;
}
.number-input__button:focus, .number-input-focus {
  border: 2px solid #0074d9;
  outline: none;
}
.number-input__button:hover::before,
.number-input__button:hover::after {
  background-color: #0074d9;
}
.number-input__button:disabled {
  opacity: 0.65;
}
.number-input__button:disabled::before,
.number-input__button:disabled::after {
  background-color: #aaa;
}
.number-input__button::before,
.number-input__button::after {
  background-color: #111;
  content: "";
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  transition: background-color 0.15s;
}
.number-input__button::before {
  height: 1px;
  width: 50%;
}
.number-input__button::after {
  height: 50%;
  width: 1px;
}
.number-input__button--minus {
  border-bottom-right-radius: 0;
  border-right: 1px solid #ddd;
  border-top-right-radius: 0;
  left: 2px;
}
.number-input__button--minus::after {
  visibility: hidden;
}
.number-input__button--plus {
  border-bottom-left-radius: 0;
  border-left: 1px solid #ddd;
  border-top-left-radius: 0;
  right: 2px;
}
.number-input__input {
  background-color: #fff;
  border: 1px solid #ddd;
  /*border-radius: 0.25rem;*/
  display: block;
  font-size: 1rem;
  line-height: 1.5rem;
  max-width: 100%;
  min-height: 1.5rem;
  min-width: 8rem;
  padding: 0.4375rem 0.875rem;
  transition: border-color 0.15s;
  width: 30px;
   /*font-weight:bold;*/
}
.number-input__input::-webkit-outer-spin-button,
.number-input__input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.number-input__input:focus {
  border: 1px solid #0074d9;
  outline: none; 
}

.wrapper-focus {
  border: 2px solid #0074d9;
  
}

.number-input__input:disabled,
.number-input__input[readonly] {
  background-color: #f8f8f8;
}
.number-input--inline {
  display: inline-block;
}
.number-input--inline > input {
  display: inline-block;
  width: 12.5rem;
}
.number-input--center > input {
  text-align: center;
}
.number-input--controls > input {
  padding-left: 3.375rem;
  padding-right: 3.375rem;
}
.number-input--small > input {
  border-radius: 0.1875rem;
  font-size: 0.875rem;
  padding: 0.25rem 0.5rem;
}
.number-input--small.number-input--inline > input {
  width: 10rem;
}
.number-input--small.number-input--controls > button {
  width: 2rem;
}
.number-input--small.number-input--controls > input {
  padding-left: 2.5rem;
  padding-right: 2.5rem;
}
.number-input--large > input {
  border-radius: 0.3125rem;
  font-size: 1.25rem;
  padding: 0.5rem 1rem;
}
.number-input--large.number-input--inline > input {
  width: 15rem;
}
.number-input--large.number-input--controls > button {
  width: 3rem;
}
.number-input__button {
	background: linear-gradient(145deg, #f3f3f3,#d5d5d5 );
	box-shadow:  1px 1px 7px #afafaf, -1px -1px 7px #dfdfdf;
}


</style>