export function zip(files) {
    // Generate the crc32 table instead of hardcoding it to avoid having a giant constant
    // in the minified output...
    const crc32_table = function() {
            const tbl = [];
            var c;
            for(var n = 0; n < 256; n++){
                    c = n;
                    for(var k =0; k < 8; k++){
                            c = ((c&1) ? (0xEDB88320 ^ (c >>> 1)) : (c >>> 1));
                    }

                    tbl[n] = c;
            }

            return tbl;
    }();

    function crc32(arr) {
            var crc = -1;
            for(var i=0; i<arr.length; i++) {
                    crc = (crc >>> 8) ^ crc32_table[(crc ^ arr[i]) & 0xFF];
            }
            return (crc ^ (-1)) >>> 0;
    }

    function putUint32s(arr, offset, ...values) {
            const dv = new DataView(arr.buffer);
            values.forEach((v,i)=>dv.setUint32(offset+i*4, v, true));
    }

    function putUint16s(arr, offset, ...values) {
            const dv = new DataView(arr.buffer);
            values.forEach((v,i)=>dv.setUint16(offset+i*2, v, true));
    }


    const records = [];
    const te = new TextEncoder('utf8');

    var offset = 0;
    var cdSz = 0;

    files.forEach(file => {
            const fh = new Uint8Array(30+file.name.length);
            const fname = te.encode(file.name);
            const chksum = crc32(file.data);

            putUint32s(fh, 0, 0x04034b50);
            putUint32s(fh, 14, chksum, file.data.length, file.data.length);
            putUint16s(fh, 26, file.name.length);

            fh.set(fname, 30);

            file.header = fh;
            file.offset = offset;

            records.push(fh);
            records.push(file.data);

            //Create CD records pending flush at the end...
            file.cdr = new Uint8Array(46+file.name.length);

            putUint32s(file.cdr, 0, 0x02014b50);
            putUint32s(file.cdr, 16, chksum, file.data.length, file.data.length);
            putUint16s(file.cdr, 28, file.name.length);
            putUint32s(file.cdr, 42, offset);

            file.cdr.set(fname, 46);

            cdSz += file.cdr.length;
            offset += file.header.length + file.data.length;
    });

    //Push all accrued CD records..
    files.forEach(f=>records.push(f.cdr));
    
    //Add EOCD record...
    const eocd = new Uint8Array(22);

    putUint32s(eocd, 0, 0x06054b50);
    putUint16s(eocd, 8, files.length, files.length);
    putUint32s(eocd, 12, cdSz, offset);

    records.push(eocd);

    return new Blob(records, {type : 'application/zip'});
}

export function CreateUUID() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

export function drawDashedPolygon(g,polygons, x, y, rotation, dash, gap, offsetPercentage){		
    g.beginPath()

    let i
    let p1
    let p2
    let dashLeft = 0
    let gapLeft = 0
    if(offsetPercentage>0){
        let progressOffset = (dash+gap)*offsetPercentage
        if(progressOffset < dash) dashLeft = dash-progressOffset
        else gapLeft = gap-(progressOffset-dash)
    }
    let rotatedPolygons = []
    for(i = 0; i<polygons.length; i++){
        let p = {x:polygons[i].x, y:polygons[i].y}
        let cosAngle = Math.cos(rotation)
        let sinAngle = Math.sin(rotation)
        let dx = p.x
        let dy = p.y
        p.x = (dx*cosAngle-dy*sinAngle)
        p.y = (dx*sinAngle+dy*cosAngle)
        rotatedPolygons.push(p)
    }
    for(i = 0; i<rotatedPolygons.length; i++){
        p1 = rotatedPolygons[i]
        if(i == rotatedPolygons.length-1) p2 = rotatedPolygons[0]
        else p2 = rotatedPolygons[i+1]
        let dx = p2.x-p1.x
        let dy = p2.y-p1.y
        let len = Math.sqrt(dx*dx+dy*dy)
        let normal = {x:dx/len, y:dy/len}
        let progressOnLine = 0
        g.moveTo(x+p1.x+gapLeft*normal.x, y+p1.y+gapLeft*normal.y)
        while(progressOnLine<=len){
            progressOnLine+=gapLeft
            if(dashLeft > 0) progressOnLine += dashLeft
            else progressOnLine+= dash
            if(progressOnLine>len){
                dashLeft = progressOnLine-len
                progressOnLine = len
            }else{
                dashLeft = 0
            }
            g.lineTo(x+p1.x+progressOnLine*normal.x, y+p1.y+progressOnLine*normal.y)
            progressOnLine+= gap
            if(progressOnLine>len && dashLeft == 0){
                gapLeft = progressOnLine-len
            }else{
                gapLeft = 0
                g.moveTo(x+p1.x+progressOnLine*normal.x, y+p1.y+progressOnLine*normal.y)
            }
        }
    }
    g.closePath()
    g.strokePath()	
}

export function Arrow(g, x1, y1, x2, y2) {
    //SOHCAHTOA = SOH ( Sin(Radians) = Opposite / Hypotenuse ) - CAH ( Cos(Radians) = Adjacent / Hypotenuse ) - TOA ( Tan(Radians) = Opposite / Adjacent

    //Set-Points for Arrow Head
    const A_start = 16;
    const O_start = 3;
    const H_start = Math.sqrt(Math.pow(A_start, 2) + Math.pow(O_start, 2));
    const change_arrow_radians = Math.atan2(O_start, A_start);

    //Calculate the change of the arrow head (i.e. horizontal, vertical or diagonal)
    const change_x_main = Math.abs(x1 - x2); //Adjacent, Calculate difference as an absolute value
    const change_y_main = Math.abs(y1 - y2); //Opposite, Calculate difference as an absolute value

    const change_angle_radians = Math.atan2(change_y_main, change_x_main);
    //const change_angle_degrees = change_angle_radians * ( 180 / Math.PI); //Not Used, but could be used as below to confirm angle of arrow head
    //alert("Angle of Arrow: " + change_angle_degrees); //remove comment

    const change_x_0_arrowhead = Math.cos(change_angle_radians + change_arrow_radians) * H_start;
    const change_y_0_arrowhead = Math.sin(change_angle_radians + change_arrow_radians) * H_start;
    const change_x_1_arrowhead = Math.cos(change_angle_radians - change_arrow_radians) * H_start;
    const change_y_1_arrowhead = Math.sin(change_angle_radians - change_arrow_radians) * H_start;

    //draw arrow
    g.beginPath();
    //g.strokeStyle = Hex_Color;


    if (((x1 < x2) && (y1 > y2)) || ((x1 < x2) && (y1 == y2)) || ((x1 == x2) && (y1 > y2))) {
        //g.moveTo(x1, y1);
        //g.lineTo(x2, y2);

        g.moveTo(x2 - change_x_0_arrowhead, y2 + change_y_0_arrowhead);
        g.lineTo(x2, y2);
        g.lineTo(x2 - change_x_1_arrowhead, y2 + change_y_1_arrowhead);

    } else if (((x1 > x2) && (y1 > y2)) || ((x1 > x2) && (y1 == y2))) {
        //g.moveTo(x1, y1);
        //g.lineTo(x2, y2);

        g.moveTo(x2 + change_x_0_arrowhead, y2 + change_y_0_arrowhead);
        g.lineTo(x2, y2);
        g.lineTo(x2 + change_x_1_arrowhead, y2 + change_y_1_arrowhead);
    } else if ((x1 < x2) && (y1 < y2)) {
        //g.moveTo(x1, y1);
        //g.lineTo(x2, y2);

        g.moveTo(x2 - change_x_0_arrowhead, y2 - change_y_0_arrowhead);
        g.lineTo(x2, y2);
        g.lineTo(x2 - change_x_1_arrowhead, y2 - change_y_1_arrowhead);

    } else if (((x1 > x2) && (y1 < y2)) || ((x1 == x2) && (y1 < y2))) {
        //g.moveTo(x1, y1);
        //g.lineTo(x2, y2);

        g.moveTo(x2 + change_x_0_arrowhead, y2 - change_y_0_arrowhead);
        g.lineTo(x2, y2);
        g.lineTo(x2 + change_x_1_arrowhead, y2 - change_y_1_arrowhead);

    }
    //g.closePath()
    g.stroke()
}

export  function dashedLine(g,x,y,x2,y2,dashArray){
    g.beginPath()
    if (!dashArray) dashArray=[10,5];
    if (dashLength==0) dashLength = 0.001; // Hack for Safari
    let dashCount = dashArray.length;
    g.moveTo(x, y);
    let dx = (x2-x), dy = (y2-y);
    let slope = dx ? dy/dx : 1e15;
    let distRemaining = Math.sqrt( dx*dx + dy*dy );
    let dashIndex=0, draw=true;
    while (distRemaining>=0.1){
        var dashLength = dashArray[dashIndex++%dashCount];
        if (dashLength > distRemaining) dashLength = distRemaining;
        let xStep = Math.sqrt( dashLength*dashLength / (1 + slope*slope) );
        if (dx<0) xStep = -xStep;
        x += xStep
        y += slope*xStep;
        
        draw ? g.lineTo(x,y) : g.moveTo(x,y);
        distRemaining -= dashLength;
        draw = !draw;
    }
    g.stroke()
    g.closePath()
}

export function convertToDec2(x) {
    return Number.parseFloat(x).toFixed(2)
}

export function sameValueInArray(spritesSelected,property) {
    let duplicate = []
    if (!spritesSelected) return
    for (let i = 0; i < spritesSelected.length; i++){
        duplicate.push(String(spritesSelected[i][property]))
    }
    let withoutDuplicate = [...new Set(duplicate)]
    if (withoutDuplicate.length === 1){
        return true
    }
    return false
}


